<template>
  <hs-cell-1 :showLogo="false">
    <div class="infor-list-cell">
      <div class="cell" @click="_extend">
        <div>
          <div>
            <div>
              <p>{{details.DepartDateTime.split(' ')[1]}}</p>
              <p v-if="language === 'CN'">{{details.DepartAirportName}}</p>
              <p v-else>{{details.DepartAirportEnName}}</p>
            </div>
            <div>
              <div class="arrow"></div><!-- 箭头 -->
              <template v-if="(details.TrainsferInfos && details.TrainsferInfos.length > 0) || (details.StopInfos && details.StopInfos.length > 0)">
                <template v-if="language === 'CN'">
                  <div class="mark">
                    <p v-if="details.TrainsferInfos.length > 0">{{(details.TrainsferInfos.length > 1 || details.StopInfos.length > 0) ? details.TrainsferInfos.length : ''}}转</p>
                    <p v-if="details.StopInfos.length > 0">{{(details.StopInfos.length > 1 || details.TrainsferInfos.length > 0) ? details.StopInfos.length : ''}}停</p>
                  </div>
                  <div class="city" v-if="details.TrainsferInfos && details.TrainsferInfos.length === 1 && (!details.StopInfos || details.StopInfos.length === 0)">{{details.TrainsferInfos[0].Name}}</div>
                  <div class="city" v-else-if="details.StopInfos && details.StopInfos.length === 1 && (!details.TrainsferInfos || details.TrainsferInfos.length === 0)">{{details.StopInfos[0].Name}}</div>
                </template>
                <div v-else class="dot"></div>
              </template>
            </div>
            <div>
              <p>{{details.ArrivalDateTime.split(' ')[1]}}<span v-if="details.AddDays > 0">+{{details.AddDays}}<template v-if="language === 'CN'">天</template></span></p>
              <p v-if="language === 'CN'">{{details.ArrivalAirportName}}</p>
              <p v-else>{{details.ArrivalAirportEnName}}</p>
            </div>
          </div>
          <div>
            <div class="logo" :style="{'background-image': `url(${details.FlightLegs[0].AirLineLogoUrl})`}"></div>
            <p>{{details.FlightLegs[0].AirLineName}}&nbsp;{{details.FlightLegs[0].FlightNo}}</p>
            <template v-if="(details.TrainsferInfos && details.TrainsferInfos.length > 0) || (details.StopInfos && details.StopInfos.length > 0)">
              <p v-if="language === 'CN'">...等</p>
              <p v-else>etc.</p>
            </template>
            <p class="share" v-if="details.FlightLegs[0].IsCodeShare">&nbsp;共享&nbsp;</p>
            <p>&nbsp;|&nbsp;</p>
            <p>{{details.Duration.split(' ').join('')}}</p>
            <p>&nbsp;|&nbsp;</p>
            <p>{{details.FlightLegs[0].Plane}}</p>
            <!-- <div class="logo" style="background-image: url('static/image/intl-plane-ticket/infor-list/time.png')"></div> -->
          </div>
        </div>
        <div>
          <p class="price" v-if="excludingTax">￥{{details.FareInfos[0].AvgPrice}}</p>
          <p class="price" v-else>￥{{details.FareInfos[0].AvgPriceWithTax}}</p>
          <p v-if="excludingTax">税￥{{details.FareInfos[0].AvgTax}}</p>
        </div>
      </div>
      <div class="details" v-if="extend">
        <div @click="extend = false">
          <p>全部舱位信息</p>
          <div class="logo" style="background-image: url('static/image/base/cell/right_arrow_gray.png')"></div>
        </div>
        <div>
          <segment :datalist="details.FlightLegs"></segment>
          <div class="item" v-for="(item, index) in details.FareInfos" :key="index">
            <div>
              <div>
                <p>{{details.CabinType}}</p>
                <p @click="_showBaggageEtc(item)">{{'退改签 行李 价格明细 >'}}</p>
              </div>
              <div>
                <p v-if="excludingTax">￥{{details.FareInfos[0].AvgPrice}}</p>
                <p v-else>￥{{details.FareInfos[0].AvgPriceWithTax}}</p>
                <p v-if="excludingTax">税￥{{details.FareInfos[0].AvgTax}}</p>
              </div>
            </div>
            <hs-button width="80px" height="40px" @click="_gotoBookDetails">预订</hs-button>
          </div>
        </div>
      </div>
    </div>
  </hs-cell-1>
</template>
<script type="text/ecmascript-6">
import HsCell1 from 'base/hs-tableview/hs-cell-1/hs-cell-1'
import HsButton from 'base/hs-button/hs-button'
import Segment from './segment'
import {mapGetters, mapMutations} from 'vuex'
export default {
  props: {
    details: {
      type: Object,
      default: function() {
        return {}
      }
    },
    /** 不含税 */
    excludingTax: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    ...mapGetters([
      'intlFlightOrder'
    ])
  },
  data() {
    return {
      extend: false
    }
  },
  methods: {
    ...mapMutations([
      'SET_INTL_FLIGHT_ORDER'
    ]),
    /** 展开 */
    _extend() {
      this.extend = !this.extend
      this.extend && this.$emit('delegateWithScrollToElement', this.details.ID)
    },
    /** 查看退改签&行李&价格明细 */
    _showBaggageEtc(item) {
      this.$emit('delegateWiteBaggageEtc', item)
    },
    /** 预订 */
    _gotoBookDetails() {
      let res = JSON.parse(JSON.stringify(this.intlFlightOrder))
      res.SelectedSegment[res.RequestSegmentIndex - 1] = this.details
      res.RequestSegmentIndex += 1
      res.SelectedSegmentIDs = res.SelectedSegment.map(v => v.ID)
      res.SelectedFareInfoID = this.details.FareInfos[0].FareInfoID
      this.SET_INTL_FLIGHT_ORDER(res)
      if (this.intlFlightOrder.RequestSegmentIndex <= this.intlFlightOrder.SearchParams.Segments.length) {
        this.$emit('delegateWithBook')
      } else {
        this.$router.push({path: '/intlPlaneTicket/bookDetails'})
      }
    }
  },
  components: {
    HsCell1,
    HsButton,
    Segment
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/base'
@import '~common/stylus/variable'
@import '~common/stylus/mixin'
.infor-list-cell
  width 100%
  >.cell
    @extend .flex-row
    @extend .flex-between
    @extend .flex-top
    width 100%
    padding 15px 0
    >div
      &:nth-child(1)
        width calc(100% - 85px)
        // width 205px
        >div
          width 100%
          &:nth-child(1)
            @extend .flex-row
            @extend .flex-between
            margin-top 5px
            margin-bottom 10px
            >div
              &:nth-child(1)
              &:nth-child(3)
                width 80px
                >p
                  width 100%
                  text-align center
                  &:nth-child(1)
                    font-size 1.5rem
                    font-weight bold
                    line-height 1.2em
                    position relative
                    >span
                      color $color_theme
                      font-size 0.6rem
                      position absolute
                      top -16px
                      right 8px
                  &:nth-child(2)
                    font-size 0.8rem
                    line-height 1.2em
              &:nth-child(2)
                width calc(100% - 160px)
                max-width 100px
                padding 0 5px
                position relative

                // 箭头
                >.arrow
                  width 100%
                  height 2px
                  background-color $color_gray_text
                  position relative
                  &:after
                    position absolute
                    content ''
                    border-top 6px transparent dashed
                    border-left 8px $color_gray_text solid
                    border-bottom 0 transparent dashed
                    border-right 6px transparent dashed
                    right -10px
                    top -4px
                >.mark
                  position absolute
                  top 50%
                  left 50%
                  transform translate(-50%, -50%)
                  background-color $color_white
                  padding 0 3px
                  >p
                    font-size 0.7rem
                    color $color_theme
                    white-space nowrap
                    display inline
                    line-height 18px
                >.city
                  font-size 0.7rem
                  color $color_theme
                  white-space nowrap
                  position absolute
                  top calc(50% + 8px)
                  left 50%
                  transform translate(-50%, 0%)
                >.dot
                  position absolute
                  top 50%
                  left 50%
                  transform translate(-50%, -50%)
                  width 10px
                  height 10px
                  border-radius 5px
                  border 2px solid $color_gray_text
                  background-color $color_white
          &:nth-child(2)
            @extend .flex-row
            @extend .flex-start
            width calc(100vw - 30px)
            margin-top 5px
            >.logo
              width 15px
              height 15px
              background-size 100% 100%
              margin-right 5px
            >p
              white-space nowrap
              color $color_gray_text
              line-height 1.2em
              font-size 0.7rem
            >.share
              color $color_theme
      &:nth-child(2)
        @extend .flex-column
        @extend .flex-start
        @extend .flex-bottom
        width 75px
        margin-top 5px
        >p
          @extend .flex-row
          @extend .flex-start
          font-size 0.7rem
          line-height 1.2em
          color $color_gray_text
        >.price
          font-size 1.3rem
          font-weight bold
          color $color_theme
  >.details
    width 100%
    >div
      width 100%
      &:nth-child(1)
        @extend .flex-row
        @extend .flex-center
        height 44px
        font-size 0.9rem
        color $color_black_text_0
        border-top 1px solid $color_gray_layer
        >.logo
          width 20px
          height 20px
          background-size 100% 100%
          transform rotate(-90deg)
          margin-left 5px
      &:nth-child(2)
        width calc(100% + 30px)
        padding 0 15px
        margin 0 -15px
        background-color $color_gray_background_0
        >.item
          @extend .flex-row
          @extend .flex-between
          width 100%
          border-top 1px solid $color_gray_layer
          padding 15px 0 5px 0
          >div
            &:nth-child(1)
              @extend .flex-row
              @extend .flex-between
              width calc(100% - 90px)
            >div
              @extend .flex-column
              @extend .flex-center
              >p
                line-height 1.2em
              &:nth-child(1)
                @extend .flex-top
                width calc(100% - 100px)
                >p
                  &:nth-child(2)
                    font-size 0.8rem
                    color $color_theme
                    margin-top 5px
              &:nth-child(2)
                @extend .flex-bottom
                width 100px
                >p
                  &:nth-child(1)
                    color $color_theme
                    font-size 1.3rem
                    font-weight bold
                  &:nth-child(2)
                    font-size 0.8rem
                    color $color_gray_text
</style>



