<template>
  <hs-dialog ref="Dialog" v-model="currentShow">
    <div class="hs-filter" @click.stop>
      <div class="header">
        <p @click="_close">{{$t('base.Cancel')}}</p>
        <p :class="[canReset ? 'canReset' : 'cannotReset']" @click="_reset">{{$t('base.ResetFilter')}}</p>
        <p @click="_confirm">{{$t('base.Confirm')}}</p>
      </div>

      <div class="details-bar">
        <div class="item" @click.stop="currentExcludingTax = !currentExcludingTax">
          <hs-checkbox v-model="currentExcludingTax"></hs-checkbox>
          <p>看不含税价</p>
        </div>
      </div>

      <div class="details">
        <div class="left" :type="language">

          <!-- <div v-if="currentDetails.DirectFilters" @click="blankIndex = 'DirectFilters'"
            class="item"
            :class="{'item-active': blankIndex === 'DirectFilters'}">
            <div class="mark" :class="{'mark-hidden': currentDetails.DirectFilters.filter(v => v.IsChecked === true).length === 0}"></div>
            <p>直飞/中转列表</p>
          </div>

          <div v-if="currentDetails.AirLineFilters" @click="blankIndex = 'AirLineFilters'"
            class="item"
            :class="{'item-active': blankIndex === 'AirLineFilters'}">
            <div class="mark" :class="{'mark-hidden': currentDetails.AirLineFilters.filter(v => v.IsChecked === true).length === 0}"></div>
            <p>航司筛选</p>
          </div>

          <div v-if="currentDetails.DepartDateFilters" @click="blankIndex = 'DepartDateFilters'"
            class="item"
            :class="{'item-active': blankIndex === 'DepartDateFilters'}">
            <div class="mark" :class="{'mark-hidden': currentDetails.DepartDateFilters.filter(v => v.IsChecked === true).length === 0}"></div>
            <p>出发时间</p>
          </div>

          <div v-if="currentDetails.ArrivalDateFilters" @click="blankIndex = 'ArrivalDateFilters'"
            class="item"
            :class="{'item-active': blankIndex === 'ArrivalDateFilters'}">
            <div class="mark" :class="{'mark-hidden': currentDetails.ArrivalDateFilters.filter(v => v.IsChecked === true).length === 0}"></div>
            <p>到达时间</p>
          </div>

          <div v-if="currentDetails.TransferCityFilters" @click="blankIndex = 'TransferCityFilters'"
            class="item"
            :class="{'item-active': blankIndex === 'TransferCityFilters'}">
            <div class="mark" :class="{'mark-hidden': currentDetails.TransferCityFilters.filter(v => v.IsChecked === true).length === 0}"></div>
            <p>中转城市</p>
          </div>

          <div v-if="currentDetails.TransferTimeFilters" @click="blankIndex = 'TransferTimeFilters'"
            class="item"
            :class="{'item-active': blankIndex === 'TransferTimeFilters'}">
            <div class="mark" :class="{'mark-hidden': currentDetails.TransferTimeFilters.filter(v => v.IsChecked === true).length === 0}"></div>
            <p>中转时长</p>
          </div>

          <div v-if="currentDetails.DepartAirPortFilters" @click="blankIndex = 'DepartAirPortFilters'"
            class="item"
            :class="{'item-active': blankIndex === 'DepartAirPortFilters'}">
            <div class="mark" :class="{'mark-hidden': currentDetails.DepartAirPortFilters.filter(v => v.IsChecked === true).length === 0}"></div>
            <p>出发机场</p>
          </div>

          <div v-if="currentDetails.ArrivalAirPortFilters" @click="blankIndex = 'ArrivalAirPortFilters'"
            class="item"
            :class="{'item-active': blankIndex === 'ArrivalAirPortFilters'}">
            <div class="mark" :class="{'mark-hidden': currentDetails.ArrivalAirPortFilters.filter(v => v.IsChecked === true).length === 0}"></div>
            <p>到达机场</p>
          </div> -->
          <div v-for="(item, index) in currentDetails" :key="index" @click="blankIndex = index"
            class="item"
            :class="{'item-active': blankIndex === index}">
            <div class="mark" :class="{'mark-hidden': noValue(JSON.stringify(item.select))}"></div>
            <p>{{item.Name}}</p>
          </div>

        </div>

        <!-- 显示选中的筛选项列表 -->
        <div class="right" v-if="currentDetails[blankIndex]">
          <hs-scroll
            class="table-view"
            ref="tableView"
            :data="currentDetails[blankIndex].Items">
            <ul>
              <!-- <hs-cell-1 @click.native="_selectAll" :showLogo="false">
                <div class="custom-cell">
                  <p>{{$t('base.All')}}</p>
                  <div v-if="currentDetails[blankIndex] && (currentDetails[blankIndex].filter(v => v.IsChecked === true).length === currentDetails[blankIndex].length)"
                    class="logo"
                    style="background-image: url('static/image/base/filter/checkbox-selected.png')"></div>
                  <div v-else
                    class="logo"
                    style="background-image: url('static/image/base/filter/checkbox-default.png')"></div>
                </div>
              </hs-cell-1>
              <hs-cell-1 @click.native="_select(index)" :showLogo="false"
                v-for="(item, index) in currentDetails[blankIndex]" :key="index">
                <div class="custom-cell">
                <p>{{item.ItemName}}</p>
                  <div v-if="currentDetails[blankIndex][index].IsChecked"
                    class="logo"
                    style="background-image: url('static/image/base/filter/checkbox-selected.png')"></div>
                  <div v-else
                    class="logo"
                    style="background-image: url('static/image/base/filter/checkbox-default.png')"></div>
                </div>
              </hs-cell-1> -->
              <hs-cell-1 @click.native="_select(index)" :showLogo="false"
                v-for="(item, index) in currentDetails[blankIndex].Items" :key="index">
                <div class="custom-cell">
                <p>{{item.ItemName}}</p>
                  <div v-if="currentDetails[blankIndex].Items[index].IsChecked"
                    class="logo"
                    style="background-image: url('static/image/base/filter/checkbox-selected.png')"></div>
                  <div v-else
                    class="logo"
                    style="background-image: url('static/image/base/filter/checkbox-default.png')"></div>
                </div>
              </hs-cell-1>
            </ul>
          </hs-scroll>
        </div>

      </div>
    </div>
  </hs-dialog>
</template>
<script type="text/ecmascript-6">
import HsCell1 from 'base/hs-tableview/hs-cell-1/hs-cell-1'
import HsCheckbox from 'base/hs-checkbox/hs-checkbox'
import HsDialog from 'base/hs-dialog/hs-dialog'
import HsScroll from 'base/hs-scroll/hs-scroll'
export default {
  props: {
    show: {
      type: Boolean,
      default: false
    },
    details: {
      type: Array,
      default: function() {
        return []
      }
    },
    /** 不含税 */
    excludingTax: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    currentShow: {
      get: function(val) {
        return this.show
      },
      set: function(val) {
        this.$emit('update:show', val)
      }
    }
  },
  data() {
    return {
      // currentDetails: {},
      // canReset: false,
      // blankIndex: 'DirectFilters',
      currentDetails: [],
      canReset: false,
      blankIndex: 0,
      currentExcludingTax: false
    }
  },
  watch: {
    currentShow(val) {
      if (val) {
        this.currentDetails = JSON.parse(JSON.stringify(this.details))
        this.currentExcludingTax = this.excludingTax
        this._setCanReset()
      }
    },
    blankIndex() {
      this.$refs.tableView && this.$refs.tableView.scrollTo(0, 0)
    }
  },
  methods: {
    /** 关闭 */
    _close() {
      this.$refs.Dialog.close()
    },
    /** 重置 */
    _reset() {
      this.currentDetails.forEach((element, index) => {
        element.Items.forEach((elementChild, indexChild) => {
          this.currentDetails[index].Items[indexChild].IsChecked = false
        })
      })
      this._setCanReset()
    },
    /** 选择全部 */
    _selectAll() {
      let isSelectedAll = this.currentDetails[this.blankIndex].filter(v => v.IsChecked === true).length === this.currentDetails[this.blankIndex].length
      this.currentDetails[this.blankIndex].map(v => {
        v.IsChecked = !isSelectedAll
        return v
      })
      this._setCanReset()
    },
    /** 选择 */
    _select(index) {
      // this.currentDetails[this.blankIndex][index].IsChecked = !this.currentDetails[this.blankIndex][index].IsChecked
      if (this.currentDetails[this.blankIndex].Items[index].ItemValue === '全部') {
        let AllIsChecked = this.currentDetails[this.blankIndex].Items[index].IsChecked
        this.currentDetails[this.blankIndex].Items.map(v => {
          v.IsChecked = !AllIsChecked
          return v
        })
      } else {
        let IsChecked = this.currentDetails[this.blankIndex].Items[index].IsChecked
        this.currentDetails[this.blankIndex].Items[index].IsChecked = !IsChecked

        let Items = this.currentDetails[this.blankIndex].Items
        let IndexOfAll = Items.map(v => v.ItemValue).indexOf('全部')
        if (IndexOfAll >= 0) {
          let AllIsChecked = true
          Items.forEach(element => {
            if (element.ItemValue !== '全部' && element.IsChecked === false) {
              AllIsChecked = false
            }
          })
          this.currentDetails[this.blankIndex].Items[IndexOfAll].IsChecked = AllIsChecked
        }
      }
      this._setCanReset()
    },
    /** 确认 */
    _confirm() {
      this.$emit('setDetails', this.currentDetails)
      this.$emit('update:excludingTax', this.currentExcludingTax)
      this._close()
    },
    /** 是否可清除筛选 */
    _setCanReset() {
      // let res = false
      // for (let key of Object.keys(this.currentDetails)) {
      //   if (this.currentDetails[key].filter(v => v.IsChecked === true).length > 0) {
      //     res = true
      //   }
      // }
      // this.canReset = res
      let res = false
      this.currentDetails.forEach(element => {
        element.Items.forEach(elementChild => {
          if (elementChild.ItemName !== '全部' && elementChild.IsChecked) {
            res = true
          }
        })
      })
      this.canReset = res
    }
  },
  components: {
    HsCell1,
    HsCheckbox,
    HsDialog,
    HsScroll
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/base'
@import '~common/stylus/variable'
.hs-filter
  position absolute
  width 100%
  left 0
  bottom 0
  background-color $color_white
  border-radius 10px 10px 0 0
  max-height 70%
  overflow hidden
  >.header
    @extend .flex-row
    @extend .flex-between
    width 100%
    height 45px
    border-bottom 1px solid $color_gray_layer
    padding 0 15px
    >p
      font-size 0.8rem
    >.cannotReset
      color $color_gray_layer
      user-select none
    >.canReset
      padding 5px
      background-color $color_gray_background_0

  >.details-bar
    @extend .flex-row
    @extend .flex-start
    @extend .flex-top
    flex-wrap wrap
    width 100%
    padding 0 20px
    border-bottom 1px solid $color_gray_layer
    >.item
      @extend .flex-row
      @extend .flex-start
      height 40px
      margin-right 30px
      >p
        font-size 0.8rem

  >.details
    @extend .flex-row
    @extend .flex-start
    @extend .flex-top
    width 100%
    height 352px
    font-size 0.8rem

    // 标题
    >.left
      width 145px
      height 100%
      background-color $color_gray_background_0
      >.item
        @extend .flex-row
        @extend .flex-start
        width 100%
        height 44px
        padding 0 10px
        >.mark
          width 6px
          height 6px
          border-radius 3px
          background-color $color_theme
          margin-right 4px
        >.mark-hidden
          visibility hidden
        >p
          width calc(100% - 30px)
      >.item-active
        background-color $color_white
    >.left[type="EN"]
      font-size 0.7rem

    // 右侧列表
    >.right
      width calc(100% - 145px)
      height 100%
      .table-view
        width 100%
        height 100%
        overflow hidden
        .custom-cell
          @extend .flex-row
          @extend .flex-between
          width 100%
          padding 10px 0
          >p
            width calc(100% - 25px)
          >.logo
            width 20px
            height 20px
            background-size 100%
</style>



