<template>
  <hs-dialog ref="Dialog" v-model="currentShow">
    <div class="hs-baggage-etc" @click.stop>
      <div class="header">
        <div class="logo"></div>
        <p>{{'退改签 行李 价格明细'}}</p>
        <div @click="close"
          class="logo" style="background-image: url('static/image/plane-ticket/query/close.png')"></div>
      </div>
      <hs-tab :options="['产品说明', '行李规定', '退改签说明']"></hs-tab>
      <hs-scroll class="details"
        :data="[details]">
        <ul>
          <div class="item">
            <div class="header">
              <div class="logo" style="background-image: url('static/image/intl-plane-ticket/infor-list/baggage.png')"></div>
              <p>行李规定</p>
            </div>
            <div class="item-details" v-for="(item, index) in details.BaggageRules" :key="index">
              <div class="title">{{item.CityPair}}</div>
              <div class="row">
                <p>成人</p>
                <p>{{item.BaggageRuleDesc}}</p>
              </div>
            </div>
          </div>
          <div class="item">
            <div class="header">
              <div class="logo" style="background-image: url('static/image/intl-plane-ticket/infor-list/rules.png')"></div>
              <p>退改签说明</p>
            </div>
            <div class="item-details">
              <div class="title">{{details.ticketsTitle}}</div>
              <div class="row">
                <p>待接入</p>
                <p>待接入</p>
              </div>
            </div>
          </div>
          <div class="item">
            <div class="header">
              <div class="logo" style="background-image: url('static/image/intl-plane-ticket/infor-list/tickets.png')"></div>
              <p>票价明细</p>
            </div>
            <div class="item-details">
              <div class="title">{{details.ticketsTitle}}</div>
              <div class="row">
                <p>成人票均价</p>
                <p>￥{{details.AvgPrice}}</p>
              </div>
              <div class="row">
                <p>税费</p>
                <p>￥{{details.AvgTax}}</p>
              </div>
            </div>
          </div>
        </ul>
      </hs-scroll>
    </div>
  </hs-dialog>
</template>
<script>
import HsCell1 from 'base/hs-tableview/hs-cell-1/hs-cell-1'
import HsDialog from 'base/hs-dialog/hs-dialog'
import HsScroll from 'base/hs-scroll/hs-scroll'
import HsTab from 'base/hs-tab/hs-tab'
export default {
  props: {
    details: {
      type: Object,
      default: function() {
        return {}
      }
    }
  },
  data() {
    return {
      currentShow: false
    }
  },
  methods: {
    show() {
      this.currentShow = true
    },
    close() {
      this.$refs.Dialog.close()
    }
  },
  components: {
    HsCell1,
    HsDialog,
    HsScroll,
    HsTab
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/base'
@import '~common/stylus/variable'
.hs-baggage-etc
  position absolute
  width 100%
  left 0
  bottom 0
  background-color $color_white
  border-radius 10px 10px 0 0
  height 80%
  overflow hidden
  >.header
    @extend .flex-row
    @extend .flex-between
    width 100%
    height 45px
    border-bottom 1px solid $color_gray_layer
    padding 0 15px
    >.logo
      width 20px
      height 20px
      background-size 100% 100%
    >p
      font-size 1.1rem
      font-weight bold
  .details
    width 100%
    height calc(100% - 45px - 40px)
    overflow hidden
    padding 0 15px
    .item
      width 100%
      padding 15px 0
      >.header
        @extend .flex-row
        @extend .flex-start
        width 100%
        margin-bottom 10px
        >.logo
          width 20px
          height 20px
          background-size 100% 100%
          margin-right 10px
        >p
          font-weight bold
      >.item-details
        width 100%
        border 1px solid $color_gray_layer
        border-bottom 0px
        margin-bottom 10px
        >.title
          @extend .flex-row
          @extend .flex-start
          width 100%
          height 30px
          padding 0 5px
          background-color $color_gray_background_0
        >.row
          @extend .flex-row
          @extend .flex-start
          width 100%
          min-height 30px
          border-bottom 1px solid $color_gray_layer
          >p
            @extend .flex-row
            @extend .flex-start
            font-size 0.8rem
            padding 5px
            min-height 30px
            &:nth-child(1)
              width 120px
              border-right 1px solid $color_gray_layer
            &:nth-child(2)
              width calc(100% - 120px)
        &:last-child
          margin-bottom 0px
</style>



