<template>
  <div class="hs-container">
    <c-header leftIcon1="return.png" :leftFn1="_back">
      <div slot="middle" class="header-title" v-if="intlFlightOrder.SearchParams.Segments[intlFlightOrder.RequestSegmentIndex - 1]">
        <h2>
          {{intlFlightOrder.SearchParams.Segments[intlFlightOrder.RequestSegmentIndex - 1].DepartObject[`AirPortName_${language}`]}}
          -
          {{intlFlightOrder.SearchParams.Segments[intlFlightOrder.RequestSegmentIndex - 1].ArrivalObject[`AirPortName_${language}`]}}
          ({{Number(intlFlightOrder.TravelType) === 0 ? $t('base.Business') : $t('base.Personal')}})</h2>
        <p>
          {{$t(intlFlightOrder.SearchParams.Segments[intlFlightOrder.RequestSegmentIndex - 1].DepartureTime, 'date', ['MM月dd日', 'MM dd'])}}
          {{getWeeks(intlFlightOrder.SearchParams.Segments[intlFlightOrder.RequestSegmentIndex - 1].DepartureTime)}}
        </p>
      </div>
      <p slot="right" class="standard" @click="showTravelStandard = true">{{$t('base.Policy')}}</p>
    </c-header>
    <hs-scroll
      class="table-view"
      ref="tableView"
      :data="datalist"
      :options="tableViewOptions"
      @pulling-down="_getDatalistFirst"
      @pulling-up="_getDatalistMore"
      :isListenScroll="true"
      @touchEnd="_touchEnd"
      @scrollStart="_scrollStart">
      <ul>
        <hs-section v-if="intlFlightOrder.SelectedSegment && intlFlightOrder.SelectedSegment.length > 0">
          <hs-cell-1 :showLogo="false">
            <div class="segments">
              <div v-for="(item, index) in intlFlightOrder.SelectedSegment" :key="index">
                <div class="logo" :style="{'background-image': `url(${item.FlightLegs[0].AirLineLogoUrl})`}"></div>
                <div class="info">
                  <p>
                    {{_showSegmentTitle(index)}}
                    {{intlFlightOrder.SearchParams.Segments[index].DepartObject[`AirPortName_${language}`]}}-{{intlFlightOrder.SearchParams.Segments[index].ArrivalObject[`AirPortName_${language}`]}}
                    ({{language === 'CN' ? item.FlightLegs[0].AirLineName : item.FlightLegs[0].AirLineEnName}})
                  </p>
                  <p>
                    {{item.DepartDate}}
                    ({{item.DepartDayOfWeek}})
                    {{item.DepartDateTime.split(' ')[1]}}
                    -
                    {{item.ArrivalDateTime.split(' ')[1]}}
                    {{item.AddDays > 0 ? `(+${item.AddDays}天)` : ''}}
                  </p>
                </div>
              </div>
            </div>
          </hs-cell-1>
        </hs-section>
        <div class="tips">
          <p>航班起降均为当地时间</p>
        </div>
        <hs-section v-for="(item, index) in datalist" :key="index">
          <infor-list-cell
            :id="`cell${item.ID.split('+').join('')}`"
            :details="item"
            :excludingTax="excludingTax"
            @delegateWiteBaggageEtc="_showBaggageEtc"
            @delegateWithScrollToElement="_ScrollToElement"
            @delegateWithBook="_book"></infor-list-cell>
        </hs-section>
        <div style="width: 100%;height: 50px;"></div>
      </ul>
    </hs-scroll>
    <div class="footer" ref="footer">
      <div class="item" @click="[Sort = Sort === 0 ? undefined : 0, _getDatalist()]">
        <div class="logo" style="background-image: url('static/image/intl-plane-ticket/infor-list/nonstop.png')">
          <div class="dot" v-if="Sort === 0"></div>
        </div>
        <p>直飞优先</p>
      </div>
      <div class="item" @click="[Sort = Sort === 1 ? 2 : 1, _getDatalist()]">
        <div class="logo" v-show="Sort !== 1 && Sort !== 2" style="background-image: url('static/image/plane-ticket/book/time.png')"></div>
        <div class="logo" v-show="Sort === 1" style="background-image: url('static/image/plane-ticket/book/down.png')"></div>
        <div class="logo" v-show="Sort === 2" style="background-image: url('static/image/plane-ticket/book/up.png')"></div>
        <p v-if="Sort === 1">从早到晚</p>
        <p v-else-if="Sort === 2">从晚到早</p>
        <p v-else>时间</p>
      </div>
      <div class="item" @click="[Sort = Sort === 7 ? 8 : 7, _getDatalist()]">
        <div class="logo" v-show="Sort !== 7 && Sort !== 8" style="background-image: url('static/image/plane-ticket/book/price.png')"></div>
        <div class="logo" v-show="Sort === 7" style="background-image: url('static/image/plane-ticket/book/down.png')"></div>
        <div class="logo" v-show="Sort === 8" style="background-image: url('static/image/plane-ticket/book/up.png')"></div>
        <p v-if="Sort === 7">从低到高</p>
        <p v-else-if="Sort === 8">从高到低</p>
        <p v-else>价格</p>
      </div>
      <div class="item" @click="showFilter = true">
        <div class="logo" style="background-image: url('static/image/plane-ticket/book/screen.png')">
          <div class="dot" v-if="hasFilters"></div>
        </div>
        <p>筛选</p>
      </div>
    </div>

    <!-- 筛选 -->
    <hs-filter-intl-plane :show.sync="showFilter" :details="details.IntlFlightFilters" :excludingTax.sync="excludingTax" @setDetails="_setDetails"></hs-filter-intl-plane>

    <!-- 行李退改签等 -->
    <baggage-etc ref="BaggageEtc" :details="baggageEtcDetails"></baggage-etc>

    <!-- 差旅标准 -->
    <c-travel-standard v-model="showTravelStandard" :details="flightRight.FlightRank" :travelPolicy="flightRight.TravelPolicy"></c-travel-standard>

  </div>
</template>
<script type="text/ecmascript-6">
import BaggageEtc from './baggage-etc'
import CHeader from 'components/c-header/c-header'
import CTravelStandard from 'components/c-travel-standard/c-travel-standard'
import HsCell1 from 'base/hs-tableview/hs-cell-1/hs-cell-1'
import HsFilterIntlPlane from 'base/hs-filter/hs-filter-intl-plane'
import HsScroll from 'base/hs-scroll/hs-scroll'
import HsSection from 'base/hs-tableview/hs-section/hs-section'
import InforListCell from './infor-list-cell'
import {kpiIntlflightList, kpiIntlflightFilt, kpiFlightGetFlightIndexInitInfo} from 'api'
import {mapGetters, mapMutations} from 'vuex'
export default {
  beforeRouteEnter(to, from, next) {
    next(vm => {
      // 检查数据完整性
      if (vm.noValue(vm.intlFlightOrder.TravelType)) {
        vm.$router.replace('/home')
        return
      }

      // 从确认页面返回
      if (from.path === '/intlPlaneTicket/bookDetails') {
        vm._back()

      // 其他
      } else {
        vm._getFlightRight()
        vm._getDatalistFirst()
      }
    })
  },
  // created() {
  //   if (this.noValue(this.intlFlightOrder.TravelType)) {
  //     this.$router.replace('/home')
  //     return
  //   }
  //   this._getDatalistFirst()
  // },
  computed: {
    ...mapGetters([
      'intlFlightOrder'
    ])
  },
  watch: {
    'details.IntlFlightFilters': {
      handler: function(val) {
        this.hasFilters = false
        if (val) {
          let res = false
          val.forEach(element => {
            if (element.Items) {
              element.Items.forEach(elementChild => {
                if (elementChild.IsChecked) {
                  res = true
                }
              })
            }
          })
          this.hasFilters = res
        }
      },
      deep: true
    }
  },
  data() {
    return {
      tableViewOptions: {
        pullDownRefresh: {
          threshold: 50
        },
        pullUpLoad: false
      },
      details: {},
      datalist: [],
      /** 差旅标准 */
      showTravelStandard: false,
      flightRight: {},
      /****************************/
      condition: { // 筛选条件: 时间,价格,筛选
        time: undefined,
        price: undefined,
        isNonstop: false
      },
      /** 排序参数 */
      Sort: undefined,
      /*******************/
      hasFilters: false, // 是否有筛选条件
      currentPageIndex: 1,
      currentPageSize: 30,
      /** 退改签&行李&价格明细 详情 */
      baggageEtcDetails: {},
      /** 筛选 */
      showFilter: false,
      /** 不含税 */
      excludingTax: false
    }
  },
  methods: {
    ...mapMutations([
      'SET_INTL_FLIGHT_ORDER'
    ]),
    /** 获取机票差旅标准 */
    _getFlightRight() {
      kpiFlightGetFlightIndexInitInfo().then((res) => {
        console.log('机票权限查询页面：', res)
        this.flightRight = res.ResultData
      })
    },
    /** 获取航班列表 不读缓存不带筛选 */
    _getDatalistFirst() {
      this.details = {}
      this.datalist = []
      this.currentPageIndex = 1
      this.tableViewOptions.pullUpLoad = false
      let params = {
        RequestSegmentIndex: this.intlFlightOrder.RequestSegmentIndex,
        SelectedSegmentID: this.intlFlightOrder.RequestSegmentIndex === 1 ? undefined : this.intlFlightOrder.SelectedSegmentIDs[this.intlFlightOrder.RequestSegmentIndex - 2],
        PageIndex: this.currentPageIndex,
        PageSize: this.currentPageSize,
        Sort: this.Sort,
        Segments: JSON.parse(JSON.stringify(this.intlFlightOrder.SearchParams.Segments)).map(v => {
          v.DepartureTime = this.beTimeNumber(v.DepartureTime)
          return v
        }),
        // DirectFlight: this.condition.isNonstop,
        FlightClass: this.intlFlightOrder.SearchParams.FlightClass,
        AdultCount: this.intlFlightOrder.SearchParams.AdultCount,
        ChildCount: this.intlFlightOrder.SearchParams.ChildCount,
        InfantCount: this.intlFlightOrder.SearchParams.InfantCount,
        AirLine: undefined
      }
      kpiIntlflightList(params).then((res) => {
        console.log('获取国际航班列表: ', res)
        this.details = res.ResultData
        this.datalist = res.ResultData.Segments
        if (res.ResultData.Segments.length >= this.currentPageSize) {
          this.tableViewOptions.pullUpLoad = {threshold: 50}
        }
        this.$refs.tableView && this.$refs.tableView.forceUpdate()
      })
    },
    /** 获取航班列表 读缓存读筛选 */
    _getDatalist() {
      this.datalist = []
      this.currentPageIndex = 1
      this.tableViewOptions.pullUpLoad = false
      let params = {
        SearchParams: {
          RequestSegmentIndex: this.intlFlightOrder.RequestSegmentIndex,
          SelectedSegmentID: this.intlFlightOrder.RequestSegmentIndex === 1 ? undefined : this.intlFlightOrder.SelectedSegmentIDs[this.intlFlightOrder.RequestSegmentIndex - 2],
          PageIndex: this.currentPageIndex,
          PageSize: this.currentPageSize,
          Sort: this.Sort,
          Segments: JSON.parse(JSON.stringify(this.intlFlightOrder.SearchParams.Segments)).map(v => {
            v.DepartureTime = this.beTimeNumber(v.DepartureTime)
            return v
          }),
          // DirectFlight: this.condition.isNonstop,
          FlightClass: this.intlFlightOrder.SearchParams.FlightClass,
          AdultCount: this.intlFlightOrder.SearchParams.AdultCount,
          ChildCount: this.intlFlightOrder.SearchParams.ChildCount,
          InfantCount: this.intlFlightOrder.SearchParams.InfantCount,
          AirLine: undefined
        },
        IntlFlightFilters: this.details.IntlFlightFilters.map(v => {
          return {
            FilterType: v.FilterType,
            SelectedValues: v.Items.filter(a => a.IsChecked).map(b => b.ItemValue)
          }
        })
      }
      kpiIntlflightFilt(params).then((res) => {
        console.log('筛选国际航班列表: ', res)
        this.datalist = res.ResultData.Segments
        if (res.ResultData.Segments.length >= this.currentPageSize) {
          this.tableViewOptions.pullUpLoad = {threshold: 50}
        }
        this.$refs.tableView && this.$refs.tableView.forceUpdate()
      })
    },
    /** 获取更多航班列表 读缓存读筛选 */
    _getDatalistMore() {
      this.currentPageIndex++
      let params = {
        SearchParams: {
          RequestSegmentIndex: this.intlFlightOrder.RequestSegmentIndex,
          SelectedSegmentID: this.intlFlightOrder.RequestSegmentIndex === 1 ? undefined : this.intlFlightOrder.SelectedSegmentIDs[this.intlFlightOrder.RequestSegmentIndex - 2],
          PageIndex: this.currentPageIndex,
          PageSize: this.currentPageSize,
          Sort: this.Sort,
          Segments: JSON.parse(JSON.stringify(this.intlFlightOrder.SearchParams.Segments)).map(v => {
            v.DepartureTime = this.beTimeNumber(v.DepartureTime)
            return v
          }),
          // DirectFlight: this.condition.isNonstop,
          FlightClass: this.intlFlightOrder.SearchParams.FlightClass,
          AdultCount: this.intlFlightOrder.SearchParams.AdultCount,
          ChildCount: this.intlFlightOrder.SearchParams.ChildCount,
          InfantCount: this.intlFlightOrder.SearchParams.InfantCount,
          AirLine: undefined
        },
        IntlFlightFilters: this.details.IntlFlightFilters.map(v => {
          return {
            FilterType: v.FilterType,
            SelectedValues: v.Items.filter(a => a.IsChecked).map(b => b.ItemValue)
          }
        })
      }
      kpiIntlflightFilt(params).then((res) => {
        console.log('筛选国际航班列表: ', res)
        this.datalist = this.datalist.concat(res.ResultData.Segments)
        if (res.ResultData.Segments.length < this.currentPageSize) {
          this.tableViewOptions.pullUpLoad = false
        } else {
          this.tableViewOptions.pullUpLoad = {threshold: 50}
        }
        this.$refs.tableView && this.$refs.tableView.forceUpdate()
      })
    },
    /** 筛选回调 */
    _setDetails(val) {
      this.details.IntlFlightFilters = val
      this._getDatalist()
    },

    /** 根据航段显示单程/往返程/N程 */
    _showSegmentTitle(index) {
      let length = this.intlFlightOrder.SelectedSegment && !this.noValue(JSON.stringify(this.intlFlightOrder.SelectedSegment)) ? this.intlFlightOrder.SelectedSegment.length : 0
      if (length === 1) {
        return this.$t('base.OneWay')
      } else if (length === 2) {
        if (this.intlFlightOrder.SelectedSegment[0].DepartAirportName === this.intlFlightOrder.SelectedSegment[1].ArrivalAirportName) {
          return index === 0 ? this.$t('base.Going') : this.$t('base.Return')
        } else {
          return this.$t('base.NoXSegment', 'function', index + 1)
        }
      } else {
        return this.$t('base.NoXSegment', 'function', index + 1)
      }
    },

    /** 查看退改签&行李&价格明细 */
    _showBaggageEtc(item) {
      this.baggageEtcDetails = item
      this.baggageEtcDetails.ticketsTitle = `
        ${this.intlFlightOrder.SearchParams.Segments[this.intlFlightOrder.RequestSegmentIndex - 1].DepartObject[`AirPortName_${this.language}`]}
        -
        ${this.intlFlightOrder.SearchParams.Segments[this.intlFlightOrder.RequestSegmentIndex - 1].ArrivalObject[`AirPortName_${this.language}`]}
      `
      this.$refs.BaggageEtc.show()
    },
    /** 预订回调 */
    _book() {
      this._getDatalistFirst()
    },
    /** 返回 */
    _back() {
      if (this.intlFlightOrder.RequestSegmentIndex === 1) {
        this.$router.go(-1)
      } else {
        let res = JSON.parse(JSON.stringify(this.intlFlightOrder))
        res.SelectedSegment.pop()
        res.RequestSegmentIndex -= 1
        res.SelectedSegmentIDs = res.SelectedSegment.map(v => v.ID)
        this.SET_INTL_FLIGHT_ORDER(res)
        this._getDatalist()
      }
    },
    /** cell展开后滑动到顶部 */
    _ScrollToElement(ID) {
      setTimeout(() => {
        this.$refs.tableView.scrollToElement(`#cell${ID.split('+').join('')}`, 300, 0, -10)
      }, 100)
    },
    _scrollStart() {
      this.$refs.footer && (this.$refs.footer.style.bottom = '-50px')
    },
    _touchEnd(pos) {
      // console.log(pos.y)
      this.$refs.footer && (this.$refs.footer.style.bottom = '0px')
    }
  },
  components: {
    BaggageEtc,
    CHeader,
    CTravelStandard,
    HsScroll,
    HsSection,
    HsCell1,
    InforListCell,
    HsFilterIntlPlane
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/base'
@import '~common/stylus/variable'
.hs-container
  width 100%
  height 100%
  .header-title
    @extend .flex-column
    @extend .flex-center
    width 100%
    height 100%
    >h2
      font-size 1.1rem
      font-weight bold
    >p
      font-size 0.6rem
      color $color_gray_text
      line-height 1.5em
  .standard
    font-size 0.9rem
    color $color_black_text_0
  .table-view
    width 100%
    height calc(100% - 45px)
    overflow hidden

    // 已选航班
    .segments
      width 100%
      padding 10px 0
      >div
        @extend .flex-row
        @extend .flex-start
        @extend .flex-top
        width 100%
        margin-bottom 5px
        >.logo
          width 15px
          height 15px
          background-size 100% 100%
          margin-right 5px
          margin-top 4px
        >.info
          width calc(100% - 20px)
          >p
            width 100%
            font-size 1rem
            line-height 1.4em
            &:nth-child(1)
              font-weight normal
            &:nth-child(2)
              font-size 0.8rem
              color $color_gray_text
        &:last-child
          margin-bottom 0px

    // 航班起降均为当地日期
    .tips
      @extend .flex-center
      width 100%
      background-color $color_gray_background_0
      >p
        color $color_gray_text
        font-size 0.9rem
        margin-top 10px
  >.footer
    @extend .flex-row
    @extend .flex-between
    transition all 300ms
    position absolute
    bottom 0
    left 0
    width 100%
    height 50px
    background-color $color-black_1
    >.item
      @extend .flex-column
      @extend .flex-center
      width 33.33%
      height 100%
      font-size 0.8rem
      color $color_white
      >.logo
        width 20px
        height 20px
        background-size 100% 100%
        margin-bottom 5px
        position relative
        >.dot
          position absolute
          top -1px
          right 0
          background-color $color_theme
          width 6px
          height 6px
          border-radius 3px
</style>



